.app{
    height: 100vh;
}

.background {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #edeff2;
}

.registerClass{
    background-color: rgba(255, 255, 255, 1);
    overflow: hidden;
    min-width: 300px;
    max-width: 50rem;
    border-radius: 50px;
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.75);
}

.containerChildrens{
    padding: 50px;
}

.containerImage{
    height: 100%;
    border-radius: 60px 0 0 60px;
}

.buttonLogin{
    background-color: #4A55A2;
    color: #ffffff;
    font-weight: bold;
}

.centerWrapper {
    padding: 20px;
    min-width: 300px;
    max-width: 500px;

    h1 {
        font-size: 3em;
        margin-top: 10px;
        margin-bottom: 5px;
        text-align: center;
    }
}

.logo {
    height: 80px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

}




.group {
    position: relative;
    margin-top: 30px;
    margin-bottom: 10px;
}

.userOption {
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.userOption p {
    color: gray;
}






.field {
    display: flex;
    flex-direction: column;
}

.field label {
    margin-top: 10px;
    margin-bottom: 10px;
}

.buttonWrapper {
    padding: 10px;
    margin-top: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    background: #005973;
    box-shadow: 0px 6px 24px 0px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    border: none;
}

.button:after {
    content: " ";
    width: 0%;
    height: 100%;
    background: #FFD401;
    position: absolute;
    transition: all 0.4s ease-in-out;
    right: 0;
}

.button:hover {
    cursor: pointer;
}

.button:hover::after {
    right: auto;
    left: 0;
    width: 100%;
}

.button span {
    text-align: center;
    text-decoration: none;
    width: 100%;
    padding: 10px 15px;
    color: #fff;
    font-size: 1.125em;
    font-weight: 700;
    letter-spacing: 0.3em;
    z-index: 20;
    transition: all 0.3s ease-in-out;
}

.button:hover span {
    color: #005973;
    animation: scaleUp 0.3s ease-in-out;
}

@keyframes scaleUp {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.95);
    }

    100% {
        transform: scale(1);
    }
}


/*loading*/
.spinner {
    width: 40px;
    height: 40px;
    position: relative;
}

.spinner .dot {
    position: absolute;
    inset: 0;
    display: flex;
    justify-content: center;
}

.spinner .dot::after {
    content: "";
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background-color: white;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.spinner .dot {
    animation: spin 2s infinite;
}

.spinner .dot:nth-child(2) {
    animation-delay: 100ms;
}

.spinner .dot:nth-child(3) {
    animation-delay: 200ms;
}

.spinner .dot:nth-child(4) {
    animation-delay: 300ms;
}

.spinner .dot:nth-child(5) {
    animation-delay: 400ms;
}
/*end loading*/

.olvido{
    text-align: center;
    display: flex;
